            }, {
                dirt: false,
                image: "Mazda_Raceway_Laguna_Seca.webp",
                name: "WeatherTech Raceway Laguna Seca Full Course",
                road: true,
                snow: false
            }, {
                dirt: false,
                image: "Willow_Springs_(Big_Willow).webp",
                name: "Willow Springs International Big Willow",
                road: true,
                snow: false
            }, {
                dirt: false,
                image: "Willow_Springs_Horse_Thief_Mile.webp",
                name: "Willow Springs International Horse Thief Mile",
                road: true,
                snow: false
            }, {
                dirt: false,
                image: "Willow_Springs_Horse_Thief_Mile.webp",
                name: "Willow Springs International Horse Thief Mile Reverse",
                road: true,
                snow: false
            }, {
                dirt: false,
                image: "Willow_Springs_(Streets_of_Willow).webp",
                name: "Willow Springs International Streets of Willow Springs",
                road: true,
                snow: false
            }, {
                dirt: false,
                image: "Willow_Springs_(Streets_of_Willow).webp",
                name: "Willow Springs International Streets of Willow Springs Reverse",
                road: true,
                snow: false
            }]
        }
    },
    mounted() {

    },
    methods: {
        getRandomInt(min = 0, max = 100) {
            return Math.floor(Math.random() * (max - min + 1)) + min
        },
        generateRace() {
            this.raceGenerated = false;
            if(!this.formValidation()) {
                return;
            }
            if(this.manufacturersIncluded) {
                this.selectedManufacturers = this.getManufacturers();
            }
            this.selectedTrack = this.getTrack();
            this.selectedPerformancePoint = this.getPerformancePoint();
            this.selectedYear = this.getYear();
            this.raceCounter++;
            this.history.unshift({
                raceCount: this.raceCounter,
                track: this.selectedTrack,
                pp: this.selectedPerformancePoint,
                year: this.selectedYear,
                radius: this.yearRadius,
                manufacturers: this.getManufacturersDisplay()
            })
            this.raceGenerated = true;
        },
        formValidation() {
            this.validationErrors = [];
            if(!Number.isInteger(this.performancePointsMin)) {
                this.validationErrors.push('PP Min is not a number!')
            }
            if(!Number.isInteger(this.performancePointsMax)) {
                this.validationErrors.push('PP Max is not a number!')
            }
            if(!Number.isInteger(this.yearMin)) {
                this.validationErrors.push('Year Min is not a number!')
            }
            if(!Number.isInteger(this.yearMax)) {
                this.validationErrors.push('Year Max is not a number!')
            }
            if(!Number.isInteger(this.yearRadius)) {
                this.validationErrors.push('Year Radius is not a number!')
            }
            if(this.performancePointsMin > this.performancePointsMax) {
                this.validationErrors.push('PP Min cannot be greater than PP Max')
            }
            if(this.yearMin > this.yearMax) {
                this.validationErrors.push('PP Min cannot be greater than PP Max')
            }
            return !this.validationErrors.length ? true : false;
        },
        getManufacturersDisplay() {
            return this.manufacturersIncluded ? this.selectedManufacturers.toString() : 'Any'
        },
        getManufacturers() {
            let mans = [];
            for (let i = 1; i <= this.manufacturersIncluded; i++) {
                mans.push(this.manufacturers[this.getRandomInt(0, this.manufacturers.length)]);
            }
            return mans;
        },
        getTrack() {
            return this.tracks[this.getRandomInt(0,this.tracks.length)];
        },
        getPerformancePoint() {
            return this.getRandomInt(this.performancePointsMin, this.performancePointsMax);
        },
        getYear() {
            return this.getRandomInt(this.yearMin, this.yearMax);
        },
        getTrackImage(track) {
            return require(`../assets/tracks/${track.image}`);
        }
    }

}
</script>
  
